<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Programmes<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newProgrammeView" title="Create"><i class="mdi mdi-plus-circle"></i></a></sup></h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <li class="breadcrumb-item"><router-link to="/console" >Home</router-link></li>
                            <li class="breadcrumb-item active">Programmes</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">
                <div class="container-fluid" >
                    
                
                    <div class="pb-5" v-if="NewProgrammeView === false">
                        
                        <div v-if="programmes.length < 1" class="text-start">
                            <b>No Programmes added yet!</b>
                        </div>

                        <div class="row no-gutters pb-5" v-else >
                            <div class="col-xl-3 col-sm-6 col-6 h-100" v-for="programme in programmes" v-bind:key="programme.id">
                                <!-- small box -->
                                <div class="small-box bg-dark h-100 pt-4">
                                    <div class="inner">
                                        <div v-if="programme.image.length === 0">
                                            <img src="@/assets/images/image.png" class="img-fluid" style="width: 8em;" />
                                        </div>
                                        <div v-else >
                                            <img :src="programme.image" class="img-fluid" style="width: 7em; height: 7em; object-fit: contain;" />
                                        </div>
                                        <!-- <i class="mdi mdi-plus-circle" v-if="programme.image === null"></i> -->
                                        <h5 class="text-truncate" :title="programme.name">{{programme.name}}</h5>
                                    </div>
                                    <div class="icon">
                                        <!-- <img :src="programme.image" class="card-img" style="width: 2em;"/> -->
                                    </div>
                                    <div class="small-box-footer mb-0 p-2">
                                        <a href="javascript:void(0)" class="text-light" @click="filterProgramme(programme.id)" >More info <i class="fas fa-arrow-circle-right"></i></a>
                                        <a href="javascript:void(0)" class="p-2 text-danger" @click="handleDeleteProgramme(programme.id)">Delete</a>
                                    </div>
                                </div>
                                

                            </div>
                        </div>


                    </div>

                    <!-- Create Programme -->
                    <div v-else >
                        <div v-if="Programme === null">
                            <form method="POST" @submit.prevent="handleNewProgramme">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadProgrammeView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Create a new programme
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                        <!-- XHR Response -->
                                        <!-- <div class="form-group text-start">
                                            <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                            <label for="profile_image" style="cursor: pointer;"><img src="@/assets/images/profile.png" class="img-fluid" style="width: 5em;"/></label>
                                        </div> -->
                                            
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="form.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Description(optional)</label>
                                            <textarea v-model="form.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Venue</label>
                                            <input type="text" v-model="form.venue" class="form-control remove-radius" placeholder="Enter venue" required/>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                        <div v-else >
                            <form method="POST" @submit.prevent="handleUpdatedProgramme">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadProgrammeView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Update programme
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                        <!-- XHR Response -->
                                        <div class="form-group text-start">
                                            <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                            <label for="profile_image" style="cursor: pointer;" v-if="Programme.image === null || Programme.image === ''" ><img src="@/assets/images/image.png" class="img-fluid" style="width: 5em;" /></label>
                                            <label for="profile_image" style="cursor: pointer;" v-else ><img :src="Programme.image" class="img-fluid" style="width: 5em;"/></label>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="Programme.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Description(optional)</label>
                                            <textarea v-model="Programme.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Venue</label>
                                            <input type="text" v-model="Programme.venue" class="form-control remove-radius" placeholder="Enter venue" required/>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                    </div>

                </div>

            </section>
        </div>

        
        <ConsoleFooter />

    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue"
import { mapActions, mapGetters } from 'vuex';

import Data from '@/lib/Data';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService';

export default {
    name: "ConsoleProgrammes",
    data(){
        return {
            NewProgrammeView: false,
            Programme: null,
            days: Data.days,
            form: {
                name: '',
                image: '',
                description: '',
                venue: '',
            }
        }
    },
    computed: mapGetters(['programmes']),
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['fetchProgrammes', 'deleteProgramme', 'createProgramme', 'updateProgramme']),
        async handleNewProgramme(){
            await this.createProgramme(this.form);
            this.NewProgrammeView = false;
            
        },
        async newProgrammeView(){
            this.NewProgrammeView = true;
            this.Programme = null;
        },
        async loadProgrammeView(){
            this.NewProgrammeView = false
            this.Programme = null;
        },
        async filterProgramme(id){
            this.Programme = this.programmes.filter(programme => programme.id === id);
            if (this.Programme.length > 0) {
                this.Programme = this.Programme[0];
                this.NewProgrammeView = null;
            }
        },
        async changeImage(){
            this.image = this.$refs.image.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'programmes');
            formData.append('tags', 'unsigned upload, programmes');
            formData.append('file', this.image);
            

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                let old_image = this.Programme.image;
                this.Programme.image = response.data.secure_url;
                await this.updateProgramme({id: this.Programme.id, programme: this.Programme});

                switch (old_image) {
                    case '':
                        break;
                    case null:
                        break;
                    default:
                        await AuthenticationService.removeImage({old_image});
                        break;
                }

            }
        },
        async handleDeleteProgramme(id){
            let programme = this.programmes.filter(programme => programme.id === id);
            // console.log(programme);
            if (programme.length > 0) {
                if (programme[0].image !== null) {
                    const response = await AuthenticationService.removeImage({old_image: programme[0].image});
                    if(response.data.error === 0){
                        await this.deleteProgramme(id);
                    }
                }
                else{
                    await this.deleteProgramme(id);
                }
            }
        },
        async handleUpdatedProgramme(){
            if (this.Programme !== null) {
                await this.updateProgramme({id: this.Programme.id, programme: this.Programme});
            }
        }
    },
    created(){
        this.fetchProgrammes();
    }


}

</script>

<style scoped>
    .small-box{
        margin-right: 1em;
    }
</style>